@import '../common/styles/theme-functions';

@mixin td-loading-theme($theme) {
  $background: map-get($theme, background);
  .td-loading-wrapper {
    &.td-overlay .td-loading {
      background: rgba(mat-color($background, dialog), 0.8);
    }
  }
}


